.Layout {
  border-left: 2px solid $complementary;
  border-right: 2px solid $complementary;
  display: flex;
  flex-direction: column;
  margin: 2.4rem 1.4rem 0 1.4rem;
  padding-top: 1.4rem;

  &__social,
  &__mail {
    width: 2.4rem;
    height: 200px;
    position: fixed;
    top: 200px;
    background-color: $primary;
  }

  &__social {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    left: 0;
    padding: 0.8rem 0;

    .twitter {
      background-image: url('..//assets/Twitter\ black.svg');
    }

    .github {
      background-image: url('../assets/Github\ black.svg');
    }

    .instagram {
      background-image: url('../assets/Instagram\ black.svg');
    }

    .linkedin {
      background-image: url('../assets/LinkedIn\ black.svg');
    }

    a {
      width: 1rem;
      height: 1rem;
      padding: 5px;
      border-radius: 50%;
      background-size: cover;
      background-repeat: no-repeat;

      &:hover {
        box-shadow: 0 0 1rem 1px $secondary;
      }
    }
  }

  &__mail {
    right: 0;
    padding: 0.8rem 0;

    a {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      span {
        color: $complementary;
        transform: rotate(90deg);
        font-size: $text-medium;

        &:hover {
          text-decoration: underline;
          text-decoration-color: #17d8ff;
        }
      }
    }
  }
}